.form-field {
	position: relative;
	display: block;
	width: 100%;
	margin-bottom: 40px;

	&[disabled],
	&.disabled {
		opacity: .5;
		pointer-events: none; }

	@include bp(600px) {
		margin-bottom: 15px; }

	&__input {
		font-family: $baseFont;
		display: block;
		width: 100%;
		padding: 0px 20px 0px;
		height: $input-h;
		line-height: $input-h-large;
		border-radius: $input-border-radius;
		border: 1px solid $gray3;
		color: $input-color;
		font-weight: $input-fw;
		font-size: $input-fs;
		transition: .3s linear;

		&:focus {
			border-color: $input-bor-color-focus;
			box-shadow: 0 4x 4px rgba(0, 0, 0, 0.25); }

		@include bp($point-2) {
			min-height: $input-h;
			line-height: $input-h;
			font-size: $input-fs-xs;
			border-radius: $input-border-radius-xs; } }

	&__textarea {
		min-height: 155px;
		line-height: 1.5;
		padding-top: 25px;
		padding-bottom: 25px;
		overflow-x: hidden;

		@include bp($point-2) {
			height: 90px; } }
	&__label {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 10px;
		padding: 0 8px;
		background: $white;
		display: flex;
		align-items: center;
		width: auto;
		height: 16px;
		font-size: 12px;
		font-weight: $input-fw;
		color: $gray-dark;
		will-change: transform;
		transform: translateY(-9px) scale(1);
		transform-origin: 20px 0;
		transition: .3s linear;
		&--top {
			align-items: flex-start; }

		@include bp($point-2) {
			font-size: $input-fs-xs; } }

	&__icon {
		position: absolute;
		top: 0;
		bottom: 0;
		right: 16px;
		margin: auto 0;
		width: 24px;
		height: 24px;
		opacity: 0;
		transition: opacity .3s linear;
		@include foil;
		background-image: url('../images/alert.svg');

		svg {
			width: 100%;
			height: 100%;
			fill: transparent; } }

	.select2-container {
		width: 100% !important; }

	&--focused {
		.form-field__label,
		.select2-selection__placeholder {
			transform: translateY(-9px) scale(1); } }


	&--error {
		.form-field__label,
		.select2-selection__placeholder {
			color: $input-color-error !important;
			transform: translate(0,-9px) scale(1); }

		.form-field__input {
			border-color: $input-color-error;
			box-shadow: 0 1px 1px rgba(240, 61, 62, 0.1); }

		.form-field__icon {
			opacity: 1;
			color: $input-color-error; }

		.select2-container .select.select2-selection--single {
			border-color: $input-color-error;
			box-shadow: 0 1px 1px rgba(240, 61, 62, 0.1); }

		.select2-selection__arrow {
			display: none; } }

	&-textarea {
		.form-field__icon {
			bottom: auto;
			top: 16px; } } }


label.error {
	display: none; }

