.form-select {
	position: relative;
	&__label {
		position: absolute;
		left: 20px;
		top: 8px;
		z-index: 20;
		font-weight: bold;
		font-size: $select-label-fs;
		line-height: $select-label-lh;
		color: $input-color; }

	&[disabled],
	&.disabled {
		opacity: .5;
		pointer-events: none; } }

.select2-container {
	* {
		outline: none!important; }
	.no-input {
		.select2-search {
			display: none; } }

	.input {
		.select2-search {
			font-size: $input-fs;
			color: $input-color;
			padding: 20px 20px 0;

			&__field {
				border-radius: $input-border-radius;
				border-color:  $input-bor-color;
				height: 48px;
				padding-left: 20px; } } }
	.select {
		height: $input-h-large;
		display: flex;
		align-items: center;

		&.select2-selection--single {
			border-radius: $input-border-radius;
			border: 0;
			background: $gray-light;

			@include bp($point-2) {
				border-radius: $input-border-radius-xs; }


			.select2-selection__rendered {
				margin: 0px 0 0;
				padding-left: 20px;
				color: $input-color;
				font-size: 16px;
				font-weight: bold;
				overflow: visible;

				@include bp($point-2) {
					font-size: $input-fs-xs; } }

			.select2-selection__arrow {
				top: 0;
				bottom: 0;
				right: 20px;
				margin: auto;
				height: 6px;
				width: 11px;
				b {
					width: 100%;
					height: 100%;
					background: url('../images/arrow.svg') 50% no-repeat;
					fill: $black;
					background-size: contain;
					border: unset;
					transition: .3s linear; } } }

		@include bp($point-2) {
			height: $input-h; } }

	.select2-selection__placeholder {
		display: block;
		transform-origin: 0 0; }

	&--default {
		.select2-selection--single {
			.select2-selection__placeholder {
				color: $baseTxt; } } }

	&--open {
		.select {
			border-bottom-color: transparent!important;
			position: relative;

			&:after {
				content: '';
				position: absolute;
				left: 0;
				right: 0;
				bottom: 0;
				margin: 0 auto;
				width: calc(100% - 40px);
				height: 1px;
				background-color: $input-bor-color; } }

		.select2-dropdown {
			border: 0;
			&--below {
				border-color: transparent;
				border-top-color: transparent;
				border-bottom-left-radius: $input-border-radius;
				border-bottom-right-radius: $input-border-radius;
				box-shadow: 0 6px 10px rgba(0, 0, 0, .1); }

			&--above {
				border-color: transparent;
				border-bottom-color: transparent;
				border-top-left-radius: $input-border-radius;
				border-top-right-radius: $input-border-radius;
				box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1); } }

		.select2-selection--single {
			.select2-selection__arrow {
				b {
					transform: scaleY(-1);
					transform-origin: 50%; } } }

		&.select2-container--above {
			.select {
				border-top-color: transparent!important;
				border-bottom-color: $input-bor-color!important;

				&:after {
					bottom: unset;
					top: 0; } } } }

	.select2-results {
		.select2-results__options {
			padding: 0;
			border: 0;

			&::-webkit-scrollbar {
				width: 0px; }

			&::-webkit-scrollbar-track {
				background: $input-bor-color;
				border-radius: $input-border-radius; }

			&::-webkit-scrollbar-thumb {
				background: #888;
				border-radius: $input-border-radius; }

			&::-webkit-scrollbar-thumb:hover {
				background: #555; }


			.select2-results__option {
				margin: 0;
				padding: 13px 18px;
				font-size: $select-option-fs;
				font-weight: 500;
				color: $input-color;
				span {
					display: block; }


				&:nth-child(odd) {
					background-color: $gray1; }

				&:after {
					display: none; }

				&--highlighted {
					color: $select-option-color-active; }

				&--highlighted[aria-selected] {
					background-color: $gray-light; }

				@include bp($point-2) {
					font-size: $select-option-fs-xs; } } } } }



.js-select + .select2-container, .js-select-input + .select2-container {
	width: 100%!important; }

.form-select__sm {
	.select2-container .select {
		height: 45px; } }
