.footer {
    margin-top: 125px;
    &__inner {
        border-top: 1px solid $gray3;
        max-width: 1630px;
        margin: 0 auto;
        padding: 70px 15px; }
    .row {
        padding: 0;
        @include flex_between_center;
        align-items: start;
        .col {
            p {
                margin: 0;
                cursor: pointer; }
            .d-block {
                margin-bottom: 30px;
                p {
                    letter-spacing: 0.01em;
                    height: 40px;
                    transition: $transition;
                    b {
                        letter-spacing: 0.05em;
                        font-weight: 800;
                        color: $baseTxt; }
                    &:hover {
                        color: $orange; } } }
            &:last-child {
                flex: 0 0 20%; }
            .social {
                margin-top: 42px;
                @include flex_center_v;
                .item {
                    margin-right: 20px; } }
            .contacts {
                .copywright {
                    font-size: 14px;
                    font-weight: 500;
                    line-height: 25px;
                    margin-bottom: 10px;
                    color: $gray-dark;
                    p {
                        height: 22px; } }
                .email__block {
                    margin: 30px 0 40px; }
                hr {
                    border-bottom: 4px solid $orange;
                    border-top: 0; }
                &__block {
                    margin-top: 40px;
                    p {
                        color: $baseTxt;
                        font-weight: 700;
                        line-height: 22px;
                        letter-spacing: 0.05em;
                        position: relative;
                        margin-bottom: 30px;
                        &:after {
                            position: absolute;
                            content: '';
                            bottom: -13px;
                            left: 0;
                            width: 35px;
                            height: 1px;
                            background: $orange; } } } } } } }





