h1 {
	font-size: $h1-fs;
	line-height: $h1-lh;
	margin: 0 0 round($baseGutter*2.933);

	@include bp($point-3) {
		margin-top: round($baseGutter*2.1);
		font-size: $h1-fs-768; } }

h2 {
	font-size: $h2-fs;
	line-height: $h2-lh;
	margin: round($baseGutter*2.16) 0 round($baseGutter*1.5); }

h3 {
	font-size: $h3-fs;
	line-height: $h3-lh;
	margin: round($baseGutter*2.16) 0 round($baseGutter*1); }

h4 {
	font-size: $h4-fs;
	line-height: $h4-lh;
	margin: round($baseGutter*1.5) 0 round($baseGutter*1); }

h5 {
	font-size: $h5-fs;
	line-height: $h5-lh;
	margin: round($baseGutter/1.875) 0 round($baseGutter/1.875); }

h6 {
	font-size: $h6-fs;
	line-height: $h6-lh;
	font-weight: 500;
	margin: round($baseGutter*1) 0 round($baseGutter*0.6); }

h1,
h2,
h3,
h4,
h5 {
	font-weight: 700; }
b {
	color: $baseTxt; }

p {
	font-size: $p-fs;
	line-height: $p-lh;
	margin: round($baseGutter/1.5) 0;
	color: $gray-dark;
	font-weight: 500;

	&+img {
		margin: round($baseGutter*0.2) 0 0; } }

ul li,
ol, li {
	font-size: $p-fs;
	line-height: $p-lh; }


img {
	&+h1,
	&+h2,
	&+h3,
	&+h4,
	&+h5,
	&+h6,
	&+p,
	&+ul,
	&+ol {
		margin-top: round($baseGutter*1); }

	&+small {
		display: block;
		text-align: center;
		margin: round($baseGutter*0.333) 0 round($baseGutter*1.5); } }

small {
	font-size: $small-fs;
	line-height: $small-lh;
	font-weight: 600; }

blockquote {
	font-size: $blockquote-fs;
	line-height: $blockquote-lh;
	color: #4263EB;
	text-align: center;
	margin: round($baseGutter*2) round($baseGutter*1.3) round($baseGutter*2);
	font-weight: normal;

	p {
		font-weight: inherit;
		font-size: 1.778em;
		line-height: 150%; } }

ul,ol {
	margin: $baseGutter round($baseGutter/1.25) round($baseGutter*1.24) 44px;

	li {
		position: relative;
		margin: round($baseGutter/1.24) 0;
		&:first-child {
			margin-top: 0; }
		&:last-child {
			margin-bottom: 0; } }

	&+img {
		margin: round($baseGutter*0.2) 0 0; } }

ol {
	list-style-type: none;
	counter-reset: item;
	li {
		&:after {
			content: counters(item, ".")". ";
			counter-increment: item;
			position: absolute;
			left: -28px;
			top: 0;
			font-size: $p-fs;
			line-height: $p-lh;
			color: $list-item-color; } } }

ul {
	li {
		&:after {
			content: '';
			position: absolute;
			left: -28px;
			top: 10px;
			width: 8px;
			height: 8px;
			border-radius: 50%;
			background-color: $list-item-color; } } }

.table-wrap {
	overflow-x: auto;
	width: 100%;
	margin: round($baseGutter*1.5) 0 round($baseGutter*2.16) 0; }

table {
	table-layout: fixed;
	min-width: 100%;

	th {
		height: 90px;
		max-width: 380px;
		padding: 0 30px;
		border: 1px solid $gray3;
		font-size: 16px;
		font-weight: 700;
		color: $baseTxt;
		white-space: nowrap;
		span {
			display: block;
			font-size: 12px;
			line-height: 16px;
			color: $gray-dark;
			font-weight: 600; } }
	td {
		height: 90px;
		padding-left: 30px; }
	td,tr, {
		border: 1px solid $gray2;
		text-align: left;
		color: $gray-dark;
		font-size: 16px;
		font-weight: 500;
		line-height: 23px; } }


h1,
h2,
h3,
h4,
h5,
h6 {
	&+p {
		margin-top: 0; } }

ul,ol,blockquote {
	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	p {
		margin: 0; } }

@include bp($point-2) {
	h1 {
		font-size: $h1-fs-480;
		line-height: $h1-lh-480;
		margin: $baseGutter 0; }

	h2 {
		font-size: $h2-fs-480;
		line-height: $h2-lh-480;
		margin: $baseGutter 0 round($baseGutter*0.6); }

	h3 {
		font-size: $h3-fs-480;
		line-height: $h3-lh-480;
		margin: $baseGutter 0 round($baseGutter*0.6); }

	h4 {
		font-size: $h4-fs-480;
		line-height: $h4-lh-480;
		margin: $baseGutter 0 round($baseGutter*0.6); }

	h5 {
		font-size: $h5-fs-480;
		line-height: $h5-lh-480;
		margin: $baseGutter 0 round($baseGutter*0.6); }

	h6 {
		font-size: $h6-fs-480;
		line-height: $h6-lh-480;
		margin: $baseGutter 0 round($baseGutter*0.6); }

	p {
		font-size: $p-fs-480;
		line-height: $p-lh-480;
		margin: round($baseGutter*0.65) 0;

		&+img {
			margin: round($baseGutter*0.2) 0 0; } }

	blockquote {
		font-size: $small-fs;
		margin: round($baseGutter*0.9) round($baseGutter*0.35); }

	ul {
		li {
			&:after {
				left: -27px;
				width: 7px;
				height: 7px;
				top: 9px; } } }

	ol {
		li {
			&:after {
				font-size: $p-fs-480;
				line-height: $p-lh-480; } } }

	ul,ol {
		margin: round($baseGutter*0.8) round($baseGutter*0.8) round($baseGutter*0.9) 38px;
		li {
			font-size: $p-fs-480;
			line-height: $p-lh-480;
			&:not(:last-child) {
				margin: round($baseGutter*0.65) 0; } }

		&+img {
			margin: round($baseGutter*0.2) 0 0; } }

	img {
		&+h1,
		&+h2,
		&+h3,
		&+h4,
		&+h5,
		&+h6,
		&+p,
		&+ul,
		&+ol {
			margin-top: round($baseGutter*0.7); }

		&+small {
			display: block;
			text-align: center;
			margin: round($baseGutter*0.22) 0 $baseGutter; } }

	.table-wrap {
		margin: round($baseGutter*1.14) 0; } }

