.breadcrumbs {
    &__list {
        display: flex;
        margin: 0 0 20px 0; }

    li {
        position: relative;
        padding-right: 20px;
        margin: 0px 20px 0px 0px;
        &:after {
            position: absolute;
            content: '/';
            top: 50%;
            left: 100%;
            transform: translateY(-50%);
            color: $gray-dark;
            font-size: 20px;
            background: transparent;
            height: 100%; }
        &:last-child:after {
            display: none; } }


    &__link {
        color: $gray-dark;
        font-size: 16px;
        font-weight: 500; } }


.main {
    &__img {
        display: block;
        @include  foil;
        background-image: url("../images/main-img.png");
        height: 709px;
        margin-bottom: 65px; }
    h2 {
        span {
            color: $orange; } }
    h6 {
        a {
            border-bottom: 1px solid transparent;
            &:hover {
                border-bottom: 1px solid $orange; } } } }



.block {
    margin-top: 82px;
    .row {
        @include flex_between_center;
        align-items: start;
        .col {
            flex: 0 0 33.333%; } }
    &__cart {
        max-width: 400px;
        img {
            display: block;
            width: 100%;
            max-height: 220px;
            overflow: hidden;
            object-fit: cover; } } }

.block2 {
    .row {
        .col {
            flex: 0 0 50%;
            .block__cart {
                max-width: 600px;
                &-img {
                    max-height: 340px;
                    margin-bottom: 38px; } }
            .block__content {
                @include flex_between_center;
                align-items: start;
                margin-top: 38px;
                h5 {
                    margin-top: 0; }
                p {
                    max-width: 400px; } } } } }


.ideas {
    max-width: 1040px; }

.links {
    margin: 30px 0 65px;
    a {
        margin-bottom: 20px; } }






